   .white_key { 
        position: absolute; 
        background-color: #fff;
        transition: background-color 0.1s ease;
        border:2px solid #000;
        border-radius:5px; 
        width: calc(1.4vw) ;
        height: calc(15vw);
        cursor:pointer;
        margin:0px 0px;
        text-align: center;
        opacity: 0.7;
        color:black;
      }

.white_key > p {
  position: relative;
  bottom: calc(-12.1vw);  
}
.white_key:hover {
        background-color:#C0C0C0;
        opacity: 0.6;
      }

      .black_key {
        position: absolute; 
        background-color: #000;
        border-radius:3px; 
        width: calc(1vw);
        height: calc(9vw);
        cursor:pointer;
        margin:0px 0px;
        
        color: white; 
        font-size: x-small;
        text-align: center;
      
      }
.black_key > p {
  position: relative;
  bottom: calc(-6.1vw); 
  
}

.black_key:hover {
        background-color:#4f4f4f;

}
